.sign-in-form {
    font-family: serif;
    width: 100%;
    height: 100%;
    position: relative;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-direction: column;
            flex-direction: column;
    background: linear-gradient(90deg, #bd5b0c, #9c851a, rgb(136, 129, 31));
}

.first-form {
    height: 60%;
    width: 40%;
    margin-bottom: 80px;
    position: relative;
    background-color: rgb(255, 255, 255);
    padding: 20px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    box-shadow: 10px 8px 8px #df8e25dc;
    border-radius: 20px;
}

.sign-in-form p{
    font-family: 'Ubuntu', sans-serif;
    color: white;
    font-size: 3rem;
    font-weight: 700;
}

div.username,
div.password {
    position: relative;
    width: 70%;
    margin-bottom: 50px;
}

div input {
    height: 100%;
    width: 100%;
    padding-top: 0px;
    color: #5fa8d3;
    border: none;
    outline: none;
    background-color: inherit;
}

div label {
    position: absolute;
    bottom: 0%;
    left: 0%;
    width: 100%;
    height: 100%;
    pointer-events: none;
    border-bottom: 1px solid black;
    transition: all 0.3s ease;
}

.first-form div .content-name::after {
    content: "";
    position: absolute;
    left: 0%;
    bottom: -2px;
    width: 0%;
    height: 100%;
    border-bottom: 3px solid #5fa8d3;
    transition: all 0.4s ease;
}

.name-span {
    position: absolute;
    bottom: 2px;
    transition: all 0.4s ease;
}

input:focus+.content-name .name-span,
input:valid+.content-name .name-span {
    -webkit-transform: translateY(-150%);
            transform: translateY(-150%);
    font-size: 0.9em;
    color: #5fa8d3;
}

input:focus+.content-name::after,
input:valid+.content-name::after {
    width: 100%;
}

input:focus+label,
input:valid+label {
    border-bottom: 1px solid transparent;
}

.button {
    
    height:40px;
    width:100px;
    background:rgb(230, 89, 8);
    border:1px solid #f2f2f2;
    border-radius:20px;
    color: rgb(245, 246, 247);
    text-transform:uppercase;
    font-family: 'Ubuntu', sans-serif;
    cursor:pointer;
}

#sign-in{
    position: absolute;
    bottom: 7%;
}

#sign-up{
    position: absolute;
    bottom: 7%;
}

.sign-up{
    border: none;
    border-radius: 10px;
    background-color: transparent;
    font-weight: 700;
    transition: all 0.3s ease;
}

.sign-up:hover{
    cursor: pointer;
    font-size: larger;
}
.profile-container{
    padding-top: 5% ;
