@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@100&display=swap');
.profile-container{
    padding-top: 5% ;
    width:100%;
    height: auto;
    background-color: rgb(82, 81, 81);
    padding-bottom: 5%;
}

.setting-container{
    padding: 20px 20px 40px 20px;
    width: 70%;
    height: auto;
    margin: auto;
    border-radius: 10px;
    background-color: white;
    margin-bottom: 20px;
}

.header{
    font-weight: bolder;
    color: #898dbf;
    padding-left: 20px;
}

.setting-content{
    width: 100%;
    display: grid;
    grid-template-columns: 1fr 3fr;
}

.profile-picture{
    padding: 0 40px 0 20px;
}

.profile-picture img{
    width: 100%;
}

.form-group, .password-form{
    padding: 0 20px 0 20px;
    width: 90%;
}

.password-form{
    margin-top: 20px;
}

.form-item{
    margin-bottom: 10px;
}

.form-item input{
    border-bottom: 0.5px solid #8f9096;
    padding: 10px 10px 5px 5px;
    font-size: 1rem;
    color: black;
}

.item-name{
    font-size: 18px;
    color:#8f9096;
    font-weight: bolder;
}
/***Button****/
.btn-container{
    display: flex;
    justify-content: center;
    padding-top: 20px;
}

form .save-btn{ 
    font-family: 'Montserrat', sans-serif;
    border-radius: 0px;
    border-color: transparent;
    background: #6c63ff;
    color:white;
    width: 80%;
    padding: 6px 12px 6px 12px;
    font-size: larger;    
    font-weight: 500;
}

button:hover{
    cursor: pointer;
}
/*****Actions*****/
.actions-container{
    padding: 20px 20px 40px 20px;
    width: 70%;
    height: auto;
    margin: auto;
    border-radius: 10px;
    background-color: white;
}

.actions-list{
    display: flex;
    justify-content: space-between;
    padding-left: 20px;
}

a{
    text-decoration: none !important;
}

.actions-container .action button{
    color: green !important;
    font-size: 20px;
    border-radius: 10px;
}

@media (max-width: 576px) {
    .setting-content{
        display: flex;
        flex-direction: column;
        align-items: center;
        row-gap: 20px;
    }

    .actions-list{
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        padding-left: 20px;
    }

    .actions-container .action button{
        margin-bottom: 20px;
    }
}
/*****Choose avatar******/
.fileContainer img,.fileContainer p{
    display: none;
}
.fileContainer{
    box-shadow: none !important;
}

.img-container{
    width: 100%;

    border-radius: 50%;
}

.notif{
    color: aliceblue;
    height: 2vh;
    text-align: center;
    font-family: 'Montserrat', sans-serif;
}

.password-form{
    display: none;
}