body{
    height: 100vh;
    padding: 0;
    margin: 0;
}
#root{
    height: 100%;
}

#msg-textarea{
    background-color: rgb(233, 230, 230);
    border-radius: 20px;
}

.ce-chat-title-text{
    color: black;
}
.ce-chat-subtitle-text{
    color:  rgb(146, 145, 145);
}

.ce-chat-feed{
    color: black;
}

.ce-chat-list-mobile-option{
    color: black !important;
}

#new-chat-plus-button{
    background-color: rgb(51, 49, 49) !important;
}

.ce-chat-form-container{
    background-image: linear-gradient(to bottom, white, rgb(211, 211, 211));
}

.ce-my-message-bubble{
    background-color: rgb(197, 56, 197) !important
}

#ce-send-message-button{
    background-color: rgb(197, 56, 197) !important;
}

.ce-settings{
    color: black !important;
}
.App-container{
    position: relative;
}   
@media (max-width: 576px){
#log-out-container{
    display: none;
}
}
@media (min-width: 576px) {
#log-out-container{
    position: absolute;
    width: 10%;
    height: 5%;
    background-image: linear-gradient(to bottom, white, rgb(211, 211, 211));
    bottom: 5%;
    right: 8%;
    border: 1px solid black;
    border-radius: 30px;
   text-align: center;
   font-weight: 700;
   transition: all 0.3ms ease;
}

    
#log-out-container:hover{
    cursor: pointer;
    background-image: linear-gradient(to bottom, rgb(175, 170, 170), rgb(221, 184, 184));
}
}



